import { createRouter, createWebHistory } from 'vue-router'
import IndexView from '../views/IndexView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: IndexView
  },
  {
    path: '/personajes',
    name: 'personajes',
    component: () => import(/* webpackChunkName: "about" */ '../views/Personajes.vue')
  },
  {
    path: '/character',
    name: 'charview',
    props:true,
    component: () => import(/* webpackChunkName: "about" */ '../views/Personaje.vue')
  },
  {
    path: '/character/:nombre/:perfil/:cumple/:rareza/:titulo/:sexo/:region/:afiliacion/:elemento/:arma',
    name: 'charview',
    props:true,
    component: () => import(/* webpackChunkName: "about" */ '../views/Personaje.vue')
  },
  {
    path: '/armas',
    name: 'armas',
    props:true,
    component: () => import(/* webpackChunkName: "about" */ '../views/ArmasView.vue')
  },
  {
    path: '/arma/:nombre/:rareza/:tipo/:img_src/:img_asc/:desc/:obtencion/:serie',
    name: 'armaview',
    props:true,
    component: () => import(/* webpackChunkName: "about" */ '../views/Arma.vue')
  },
  {
    path: '/mapa',
    name: 'mapa',
    props:true,
    component: () => import(/* webpackChunkName: "about" */ '../views/MapaView.vue')
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
